/* CartItem.css */
.cart-item {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    margin-bottom: 10px;
    background-color: #f4f4f4;
    border-radius: 8px;
  }
  
  .cart-item img {
    width: 50px;
    height: 50px;
  }
  
  .cart-item-info {
    flex: 1;
    margin-left: 10px;
  }
  
  .cart-item .btn {
    background-color: #28a745;
    color: white;
    padding: 5px 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  