/* CartPage.css */
.cart-page {
    padding: 20px;
  }
  
  .cart-page h2 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .cart-page .cart-items {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .cart-page .cart-total {
    margin-top: 20px;
    font-size: 1.2rem;
    text-align: right;
  }
  