/* ProductListPage.css */
.product-list-page {
    padding: 20px;
  }
  
  .product-list-page h2 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .product-list-page .product-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .product-list-page .product-list .product-card {
    margin-bottom: 20px;
  }
  