/* CheckoutPage.css */
.checkout-page {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .checkout-page h2 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .checkout-page .form-group {
    margin-bottom: 15px;
  }
  
  .checkout-page .form-group label {
    font-size: 1rem;
    margin-bottom: 5px;
  }
  
  .checkout-page .form-group input {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .checkout-page .btn {
    background-color: #28a745;
    color: white;
    padding: 12px 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .checkout-page .btn:hover {
    background-color: #218838;
  }
  